.section1 {
    padding: 10px 20px;
    background-color: var(--white-color);
    margin: 0 auto;
    &__title {
        font-size: 30px;
        font-weight: 900;
        position: relative;
        text-transform: uppercase;
        margin-top: 30px;
        text-align: center;
        width: 100%;
        @media screen and (max-width: 440px) {
            font-size: 24px;
        }
        // &::after {
        //     width: 150px;
        //     height: 0;
        //     position: absolute;
        //     top: 100%;
        //     left: 50%;
        //     margin-left: -48.5px;
        //     margin-top: 10px;
        //     border-bottom: 8px solid var(--special-text-color);
        //     content: '';
        // }
    }
    &__about {
        width: 84.7%;
        margin: 88px auto;
        display: block;
        font-size: 20px;
        text-align: center;
        color: #3f3f3f;
        @media screen and (max-width: 440px) {
            font-size: 16px;
        }
        p {
            margin: 40px 0;
            color: inherit;
        }
    }
}