.brandsection {
    background-position: center top,center bottom;
    background-repeat: no-repeat,no-repeat;
    background-color: #ececec;
    height: 650px;
    background-image: url(../../assets/background.webp);
    .brands1, .brands2 {
        margin-bottom: 90px;
        img{
            max-width: 150px;
            max-height: 80px;
        }
    }
    .section1__title.brands {
        color: white !important;
        padding: 50px 0 0;
    }
    &__subtitle {
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center;
        color: white !important;
        padding: 10px 0;
        margin-bottom: 25px;
    }
}