.navbar .navbar-brand {
    font-size: 30px;
  }
  .navbar .nav-item {
    padding: 10px 20px;
    @media screen and (max-width: 440px) {
      margin-top: 15px;
    }
  }
  .navbar .nav-link {
    font-size: 20px;
    margin-left: 10px;
    color: var(--white-color);
    position: relative;
    &::after {
      content: "";
      position: absolute;
      width: 0%;
      background: var(--special-text-color);
      height: 2px;
      bottom: 0;
      left: 0;
      transition: width ease-in .5s;
    }
    &:hover::after {
      width: 100%;
    }
    @media screen and (max-width: 440px) {
      &:hover::after {
        width: 50%;
      }
      .navbar-collapse {
        background: rgba(0,0,0,0.8);
        border-radius: 15%;
        padding: 35px 10px;
        position: relative;
      }
    }
  }
  .fa-bars {
    color: var(--special-text-color);
    font-size: 30px;
  }
.navbar-toggler {
  outline: none !important;
  box-shadow: unset !important;
}


  .btn-whatsapp-pulse {
    background: #25d366;
    color: white;
    font-size: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 0;
    height: 0;
    padding: 30px;
    text-decoration: none;
    border-radius: 50%;
    animation-name: pulse;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-iteration-count: infinite;
    &:hover {
      color: var(--white-color) !important;
    }
  }
  
  @keyframes pulse {
    0% {
      box-shadow: 0 0 0 0 rgba(37, 211, 102, 0.5);
    }
    80% {
      box-shadow: 0 0 0 14px rgba(37, 211, 102, 0);
    }
  }
  
  .btn-whatsapp-pulse-border {
    bottom: 120px;
    right: 20px;
    animation-play-state: paused;
  }
  
  .btn-whatsapp-pulse-border::before {
    content: "";
    position: absolute;
    border-radius: 50%;
    padding: 25px;
    border: 5px solid #25d366;
    opacity: 0.75;
    animation-name: pulse-border;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-iteration-count: infinite;
  }
  
  @keyframes pulse-border {
    0% {
      padding: 25px;
      opacity: 0.75;
    }
    75% {
      padding: 50px;
      opacity: 0;
    }
    100% {
      opacity: 0;
    }
  }

$color: #111;
$primary: rgba(254,214,4, 0.8);

.cta {
  position: relative;
  margin: auto;
  padding: 10px 14px;
  transition: all .2s ease;
  color: #fff !important;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    border-radius: 28px;
    background: rgba($primary,.5);
    width: 40px;
    height: 40px;
    transition: all .5s ease;
  }
  span {
    position: relative;
    font-size: 14px;
    line-height: 18px;
    font-weight: 900;
    letter-spacing: .25em;
    vertical-align: middle;
  }
  svg {
    position: relative;
    top: 0;
    margin-left: 10px;
    fill: none;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke: #fff;
    stroke-width: 2;
    transform: translateX(-5px);
    transition: all .3s ease;
  }
  &:hover {
    &:before {
      width: 100%;
      background: rgba($primary,1);
      color: var(--white-color) !important;
    }
    svg {
      transform: translateX(0);
    }
  }
  &:active {
    transform: scale(.96);
  }
}


