.containerlag {
    width: 500px;
    position: relative;
   }
   
   .outer-col {
    position: fixed;
    right: 5px;
    bottom: -399px;
    width: 500px;
    z-index: 9;
   }
   
   .heading {
    float: left;
    width: 100%;
    background: var(--main-color);
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    padding: 10px;
    font-family: arial;
    border-radius: 5px 5px 0 0;
    cursor: pointer;
   }
   
   .form-col {
    float: left;
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    height: 400px;
    padding: 10px;
    border: 1px solid #999;
    background: white;
   }







   *,
*:focus{outline: none}

.form-item{
  position: relative;
  margin-bottom: 15px
}
.form-item input{
  display: block;
  width: 400px;
  height: 40px;
  background: transparent;
  border: solid 1px #ccc;
  transition: all .3s ease;
  padding: 0 15px
}
.form-item textarea {
    display: block;
  width: 400px;
  background: transparent;
  border: solid 1px #ccc;
  transition: all .3s ease;
  padding: 0 15px
}
.form-item input:focus{
  border-color: blue
}
.form-item textarea:focus{
  border-color: blue
}
.form-item label{
  position: absolute;
  cursor: text;
  z-index: 2;
  top: 13px;
  left: 10px;
  font-size: 12px;
  font-weight: bold;
  background: #fff;
  padding: 0 10px;
  color: #999;
  transition: all .3s ease
}
.form-item input:focus + label,
.form-item input:valid + label{
  font-size: 11px;
  top: -5px
}
.form-item input:focus + label{
  color: blue
}
